import React from 'react';

const About = () => {
  return (
    <div className='container'>
      <h2>Vår Historie og Felleskap på Veien</h2>
      <p>Heiersmed'n MC Touring ble grunnlagt i 2020, da en liten gjeng med familie og venner møttes for sin første felles tur på Gvarv i Telemark. Ideen om å starte en klubb oppsto ut fra familiens økende interesse for motorsykkel, og ønsket om å ha en sosial arena der vi kunne dele gleden over friheten på to hjul. Gjennom årene har vi åpnet klubben for venner og nye medlemmer, og i dag ønsker vi å være et fellesskap som samler motorsykkelentusiaster på tvers av aldre og erfaring.</p>
      <h3>En Åpen Vei for Alle Motorsykkelentusiaster</h3>
      <p>Vi i Heiersmed'n MC Touring ønsker å skape en åpen og inkluderende klubb for alle som deler en lidenskap for motorsykler. Vår visjon er å være en sosial arena der både erfarne og nye entusiaster kan finne glede, trygghet og fellesskap på veien. Enten du kommer alene eller tar med familien, håper vi å tilby et fellesskap som verdsetter mangfold og en felles kjærlighet for motorsykkelkjøring.</p>
      <h3>Et Fellesskap for Alle Aldre og Erfaringer</h3>
      <p>Hos Heiersmed'n MC Touring er alle som ønsker en behagelig og koselig atmosfære, og som har en interesse for motorsykler, hjertelig velkomne. Vi er en klubb med et bredt aldersspekter, hvor både unge og eldre finner sin plass. Enten du er ny på veien eller har mange års erfaring, finner du et fellesskap som setter pris på mangfold og en inkluderende kjøreopplevelse.</p>
      <h3>Eventyr på Veien – Årlige Turer og Lokale Kjøreturer</h3>
      <p>Vi har som mål å arrangere minst én årlig fellestur for hele klubben, slik at alle kan samles og dele opplevelsen. I tillegg arrangerer vi flere turer gjennom året for de som har mulighet til å bli med. Våre turer går ofte rundt i Østfold, der størstedelen av klubben holder til, og på Vestlandet, hvor flere av våre medlemmer også er basert. Vi ønsker å skape turer som alle kan delta på, uansett hvor de bor, og som styrker fellesskapet i klubben.</p>
      <h3>Trygge Turer, Gode Opplevelser</h3>
      <p>I Heiersmed'n MC Touring legger vi vekt på trygge og hyggelige kjøreturer der vi tar oss god tid. Hos oss er det opplevelsen og gleden ved turen som står i fokus, ikke hastigheten. Vi kjører sammen for hyggen og fellesskapet, og racing er derfor ikke en del av våre turer. Vårt mål er å sikre at alle, uansett erfaring, kan nyte en trygg og behagelig kjøreopplevelse.</p>
    </div>
  );
}

export default About;
